import {
  OnChanges,
  Input,
  Output,
  EventEmitter,
  SimpleChanges,
  ViewChild
} from '@angular/core';
import { DxPopupComponent, DxLoadPanelComponent } from 'devextreme-angular';
import { ToastrService } from 'ngx-toastr';
import * as _ from 'lodash';

import { BaseLIBModel } from './base-lib.model';
import { BaseAPIService } from '../base-api.service';

export class BaseFormLIBComponent implements OnChanges {
  @ViewChild('popup') popup: DxPopupComponent;
  @ViewChild('loadPanel') loadPanel: DxLoadPanelComponent;

  @Input() visible: boolean;
  @Input() isCreating: boolean;
  @Input() id: string;
  @Output() closed = new EventEmitter<void>();
  @Output() saved = new EventEmitter<void>();

  name = 'Chức vụ';
  data: BaseLIBModel = new BaseLIBModel();
  oldData: BaseLIBModel;

  constructor(
    protected service: BaseAPIService,
    protected toastr: ToastrService
  ) {}

  ngOnChanges(e: SimpleChanges) {
    if (e.visible && e.visible.currentValue === true) {
      if (this.isCreating === true) {
        // Creat
        this.popup.title = `Thêm ${this.name}`;
        this.data = new BaseLIBModel();
        this.popup.instance.show();
      } else {
        // Edit
        this.popup.title = `Sửa ${this.name}`;
        this.loadPanel.instance.show();
        this.service.getById(this.id).subscribe(
          (res: BaseLIBModel) => {
            this.data = _.clone(res);
            this.oldData = _.clone(res);
            this.popup.instance.show();
            this.loadPanel.instance.hide();
          },
          () => {
            this.toastr.error('Thất bại', 'Lấy dữ liệu');
            this.closed.emit();
            this.loadPanel.instance.hide();
          }
        );
      }
    }
  }

  onHidden(e) {
    this.closed.emit();
  }

  onClose(e) {
    this.popup.instance.hide();
  }

  onSave(e) {
    e.preventDefault();
    if (this.isCreating === true) {
      // Create
      this.loadPanel.instance.show();

      this.service.create(this.data).subscribe(
        () => {
          this.saved.emit();

          this.popup.instance.hide();
          this.loadPanel.instance.hide();

          this.toastr.success('Thành công', `Thêm ${this.name}`);
        },
        (res: any) => {
          // Message
          switch (res.status) {
            case 409:
              this.toastr.error('Đã tồn tại', `Mã <${this.data.Id}>`);
              break;
            default:
              this.toastr.error('Thất bại', `Thêm ${this.name}`);
          }

          this.loadPanel.instance.hide();
        }
      );
    } else {
      // Edit
      if (!_.isEqual(this.data, this.oldData)) {
        // Changed
        this.loadPanel.instance.show();
        this.service.edit(this.data.Id, this.data).subscribe(
          () => {
            this.saved.emit();

            this.popup.instance.hide();
            this.loadPanel.instance.hide();

            this.toastr.success('Thành công', `Lưu ${this.name}`);
          },
          () => {
            this.toastr.error('Thất bại', `Lưu ${this.name}`);

            this.loadPanel.instance.hide();
          }
        );
      } else {
        // Not Changed
        this.popup.instance.hide();
      }
    }
  }
}
