import { SecureService } from './shared/secure.service';
import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { HttpClientModule } from '@angular/common/http';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';

import { AppRoutingModule } from './app-routing.module';
import { AppComponent } from './app.component';
import { AboutComponent } from './about/about.component';
import { ContactComponent } from './contact/contact.component';
import { HomeComponent } from './home/home.component';

// --- LAYOUT ---
import { LeftSidebarComponent } from './layout/left-sidebar/left-sidebar.component';
import { TopBarComponent } from './layout/top-bar/top-bar.component';
import { LoaderComponent } from './layout/loader/loader.component';
import { RightSidebarComponent } from './layout/right-sidebar/right-sidebar.component';

// --- LIB ---
// Nhân sự
import { ChucVuModule } from './lib-nhan-su/chuc-vu/chuc-vu.module';
import { ChucVuFormModule } from './lib-nhan-su/chuc-vu/chuc-vu-form/chuc-vu-form.module';
import { ChucVuDatagridModule } from './lib-nhan-su/chuc-vu/chuc-vu-datagrid/chuc-vu-datagrid.module';

@NgModule({
    declarations: [
        AppComponent,
        AboutComponent,
        ContactComponent,
        HomeComponent,
        // --- LAYOUT ---
        LeftSidebarComponent,
        TopBarComponent,
        LoaderComponent,
        RightSidebarComponent
    ],
    imports: [
        BrowserModule,
        BrowserAnimationsModule,
        AppRoutingModule,
        HttpClientModule,
        // SPA
        ChucVuModule,
        ChucVuDatagridModule,
        ChucVuFormModule
    ],
    providers: [SecureService],
    bootstrap: [AppComponent]
})
export class AppModule { }
