import { Injectable, OnChanges, SimpleChanges, ViewChild, Input, Output, EventEmitter, OnInit } from '@angular/core';
import { DxPopupComponent, DxLoadPanelComponent, DxButtonComponent } from 'devextreme-angular';
import { ToastrService } from 'ngx-toastr';

import { BaseAPIService } from './base-api.service';
import * as _ from 'lodash';
import { DEFAULT_LOAD_PANEL } from '../app.config';

@Injectable()
export class BaseDeleteComponent implements OnInit, OnChanges {
  @ViewChild('popup') popup: DxPopupComponent;
  @ViewChild('loadPanel') loadPanel: DxLoadPanelComponent;
  @ViewChild('btnYes') btnYes: DxButtonComponent;
  @ViewChild('btnNo') btnNo: DxButtonComponent;

  @Input() visible: boolean;
  @Input() id: string;
  @Output() closed = new EventEmitter<void>();
  @Output() deleted = new EventEmitter<void>();

  constructor(
    protected service: BaseAPIService,
    protected toastr: ToastrService
  ) {}

  ngOnChanges(e: SimpleChanges) {
    if (e.visible && e.visible.currentValue === true) {
      this.popup.instance.show();
    }
  }

  ngOnInit() {
    // Popup
    this.popup.showTitle = false;
    this.popup.width = 'auto';
    this.popup.height = 'auto';
    this.popup.resizeEnabled = false;
    this.popup.maxWidth = '600px';
    this.popup.shadingColor = 'rgba(0, 0, 0, 0)';
    // Load Panel
    this.loadPanel = _.extend(this.loadPanel, DEFAULT_LOAD_PANEL);
    // Button Yes
    this.btnYes.icon = 'save';
    this.btnYes.tabIndex = 0;
    this.btnYes.text = 'Có';
    this.btnYes.type = 'success';
    this.btnYes.width = 80;
    // Button No
    this.btnNo.icon = 'clear';
    this.btnNo.tabIndex = 1;
    this.btnNo.text = 'Không';
    this.btnNo.type = 'danger';
    this.btnNo.width = 80;
  }

  onDelete(e) {
    if (this.id !== undefined) {
      this.loadPanel.instance.show();

      this.service.delete(this.id).subscribe(
        () => {
          this.deleted.emit();

          this.popup.instance.hide();
          this.loadPanel.instance.hide();

          this.toastr.success('Thành công', 'Xóa chức vụ');
        },
        () => {
          this.loadPanel.instance.hide();

          this.toastr.error('Thất bại', 'Xóa chức vụ');
        }
      );
    }
  }

  onHidden(e) {
    this.closed.emit();
  }

  onClose(e) {
    this.popup.instance.hide();
  }

}
