import { Component, OnInit, ViewChild } from '@angular/core';
import { DxDataGridComponent } from 'devextreme-angular';

import { ChucVu } from '../shared/chuc-vu';
import { ChucVuService } from '../shared/chuc-vu.service';
import { ChucVuForm } from '../shared/chuc-vu-form';
import { ChucVuDelete } from '../shared/chuc-vu-delete';

@Component({
  selector: 'app-chuc-vu-datagrid',
  templateUrl: './chuc-vu-datagrid.component.html',
  styleUrls: ['./chuc-vu-datagrid.component.css']
})
export class ChucVuDatagridComponent implements OnInit {
  @ViewChild ('dataGrid') dataGrid: DxDataGridComponent;

  dataSource: any = {};
  chucvu: ChucVu = new ChucVu();
  chucvuForm: ChucVuForm = new ChucVuForm();
  chucvuDelete: ChucVuDelete = new ChucVuDelete();

  constructor(private chucvuService: ChucVuService) {}

  ngOnInit() {
    this.dataSource = this.chucvuService.getList();
  }

  onToolbarPreparing(e) {
    e.toolbarOptions.items.unshift(
      {
        // Thêm
        location: 'before',
        widget: 'dxButton',
        options: {
          hint: 'Thêm',
          icon: 'add',
          type: 'success',
          text: 'Thêm',
          onClick: () => {
            this.create();
          }
        }
      },
      {
        // Sửa
        location: 'before',
        widget: 'dxButton',
        options: {
          hint: 'Sửa',
          icon: 'edit',
          type: 'default',
          text: 'Sửa',
          onClick: () => {
            this.edit();
          },
          bindingOptions: {
            disabled: '!data.id'
          }
        }
      },
      {
        // Xóa
        location: 'before',
        widget: 'dxButton',
        options: {
          hint: 'Xóa',
          icon: 'trash',
          type: 'danger',
          text: 'Xóa',
          onClick: () => {
            this.delete();
          },
          bindingOptions: {
            disabled: '!data.id'
          }
        }
      },
      {
        // Refresh
        location: 'after',
        widget: 'dxButton',
        options: {
          hint: 'Refresh',
          icon: 'refresh',
          onClick: () => {
            this.dataGrid.instance.getDataSource().reload();
          }
        }
      }
    );
  }
  onSelectionChanged(e) {
    if (e.selectedRowsData.length > 0) {
      // Lấy dữ liệu cho model
      const selectedItem = e.selectedRowsData[0];
      this.chucvu.id = selectedItem.id;
    }
  }
  onRowDblClick(e) {
    this.edit();
  }

  create() {
    this.chucvuForm.isCreating = true;
    this.chucvuForm.visible = true;
  }
  edit() {
    this.chucvuForm.id = this.chucvu.id;
    this.chucvuForm.isCreating = false;
    this.chucvuForm.visible = true;
  }
  onSaved(e) {
    this.dataGrid.instance.getDataSource().reload();
  }
  onCloseForm(e) {
    this.chucvuForm.visible = false;
  }

  delete() {
    this.chucvuDelete.id = this.chucvu.id;
    this.chucvuDelete.visible = true;
  }
  onDeleted(e) {
    this.dataGrid.instance.getDataSource().reload();
  }
  onCloseDelete(e) {
    this.chucvuDelete.visible = false;
  }

}
