  template: `
    <dx-popup #popup class="popup" (onHidden)="onHidden($event)">
      <div style="text-align: center">Bạn có chắc chắn muốn xóa?</div>
      <br />
      <dx-button #btnNo class="float-left" style="margin-right: 10px" (onClick)="onClose($event)"></dx-button>
      <dx-button #btnYes class="float-right" (onClick)="onDelete($event)"></dx-button>
      <br />
    </dx-popup>
    <dx-load-panel #loadPanel></dx-load-panel>
  `
