@Injectable({ providedIn: 'root' })
export class BaseAPIService {
  ODATA_URL: string;
  API_URL: string;

  constructor(protected http: HttpClient, protected toastr: ToastrService) {}

  // --- API 2.0 ---
  getList(): any {
    return new DataSource({
      key: 'id',
      load: (loadOptions: any) => {
        let params: HttpParams = new HttpParams();
        // let selectArray = select;
        LIST_OPTION_DATAGRID.forEach((i: string) => {
          if (
            i in loadOptions &&
            loadOptions[i] !== null &&
            loadOptions[i] !== '' &&
            loadOptions[i] !== undefined
          ) {
            params = params.set(i, JSON.stringify(loadOptions[i]));
          }
        });

        return this.http
          .get(this.API_URL, { params, headers: httpOptions.headers} )
          .toPromise()
          .then(
            (res: any) => {
              return res;
            },
            () => {
              this.toastr.error('Thất bại', 'Lấy dữ liệu');
            }
          );
      },

      update: (key: string, values: any) => {
        return this.http.patch(`${this.ODATA_URL}('${key}')`, values).subscribe(
          () => this.toastr.success('Thành công', 'Lưu'),
          () => this.toastr.error('Thất bại', 'Lưu')
        );
      }
    });
  }

  // --- OData ---
  getById(id: string): Observable<object> {
    return this.http.get(`${this.ODATA_URL}('${id}')`, { headers: httpOptions.headers });
  }
  create(data: any): Observable<object> {
    return this.http.post(this.ODATA_URL, data, { headers: httpOptions.headers });
  }
  edit(id: string, data: any): Observable<object> {
    return this.http.put(`${this.ODATA_URL}('${id}')`, data, { headers: httpOptions.headers });
  }
  delete(id: string): Observable<object> {
    return this.http.delete(`${this.ODATA_URL}('${id}')`, { headers: httpOptions.headers });
  }
}
